<template>

</template>

<script>
export default {
  name: "order",
  data () {
    return {
      info: {
        country: {
          id: null,
          name: null
        },
        city: {
          id: null,
          name: null,
          pickup: null
        },
        phone: null,
        name: null,
        email: null,
        address: null,
        post_code: null,
      },
      code: null,         // 
      sale: false,        //  
      price_with_sale: 0, //   
      transfer: {
        price: 0,
        name: null
      },
      method_pay: null,
      loaderButton: false,
      loaderButtonAfter: false,
      windowsLoader: false,
      order: {
        no: null,
        id: null
      }
    }
  },
  mounted () {
    this.$nextTick(() => {
      this.info.phone     = $('#phone').val()
      this.info.email     = $('#email').val()
      this.info.name      = $('#name').val()
      this.info.address   = $('#address').val()
      this.info.post_code = $('#post_code').val()
    })
  },
  methods: {
    setCountry (country) {
      this.info.country = country
    },

    setCity (city) {
      this.info.city = city
    },

    setName (event) {
      this.info.name = event.target.value
    },

    setEmail (event) {
      this.info.email = event.target.value
    },
    setPhone (event) {
      this.info.phone = event.target.value
    },

    setAddress (event) {
      this.info.address = event.target.value
    },

    setPostCode(event) {
      this.info.post_code = event.target.value
    },

    setPickupTransfer () {
      this.transfer = {
        price: 0,
        name: 'pickup'
      }
    },

    resetTransfer () {
      this.transfer = {
        price: 0,
        name: null
      }
    },

    checkSale () {
      window.axios.post('/api/coupon', {
        code: this.code,
        items: this.$store.getters.productsCart
      })
      .then(response => {
        if(response.data.sale) {
          this.sale = true
          this.price_with_sale = response.data.sale
        }
      })
      .catch(error => {
        window.Swal.fire({
          icon: "error",
          title: '',
          text: error.response.data.error
        })
        this.code = null
      })
    },
    pay () {
      !this.$root.test ? this.$store.commit('clearCart') : null
      let widget = new cp.CloudPayments();
      widget.pay('auth', //  'charge'
        { //options
          publicId: 'pk_e531fbe6a10b284d414ce62fbf852', //id   
          description: '  dockuboardhouse.com', //
          amount: this.price, //
          currency: this.$store.state.currency.short_name, //
          invoiceId: this.order.no, //   ()
          accountId: this.info.email, //  ()
          skin: "modern", //  ()
          // data: {
            // myProp: 'myProp value'
          // }
        },
        {
          onSuccess: (options) => { // success
            //   
            console.log(options)
            this.windowsLoader = true
            window.axios.post('/order/update/status', {
              order: this.order.id,
              state: 'pending'
            })
            .then(response => {
              this.loaderButton = false
              window.location = '/order'
            })
            .catch(error => {
              window.Swal.fire({
                icon: "error",
                title: '',
                text: '    .   '
              })
            })

          },
          onFail: (reason, options) => { // fail
            //   
            console.log(reason, options)
            this.loaderButton = true
            window.Swal.fire({
              title: '   !',
              text: '  3-      ,   ',
              icon: 'error',
              confirmButtonText: ''
            })
              .then((result) => {
                window.location = '/order'
              })
          }
        }
      )
    },
    createOrder () {
      return window.axios.post('/order/store', {
        info: this.info,
        method_pay: this.method_pay,
        transfer: this.transfer,
        items: this.$store.getters.productsCart,
        code: this.code,
        price: this.priceAmount,
        sale: this.price_with_sale
      })
    },
    cashPay () {
      this.createOrder()
        .then(response => {
          console.log(response)
          !this.$root.test ? this.$store.commit('clearCart') : null

          window.Swal.fire({
            title: '',
            text: '  ',
            icon: 'success',
            confirmButtonText: ''
          })
          .then(result => {
            window.location = '/order'
          })

        })
        .catch(error => {
          let errors = Object.values(error.response.data.errors)
          errors = errors.flat()
          console.log(errors)
          let txt = ''
          errors.forEach(value => {
            txt += ('<p>' + value + '</p>')
          })

          window.Swal.fire({
            title: '',
            html: txt,
            icon: 'error',
            confirmButtonText: '',
            width: '40rem'
          })
          .then(result => {
            this.loaderButtonAfter = false
          })
        })
    },
    orderedNow () {
      this.loaderButton = true;
      this.createOrder()
        .then(response => {
          this.order = response.data.order
          this.pay()
        })
        .catch(error => {
          let errors = Object.values(error.response.data.errors)
          errors = errors.flat()
          console.log(errors)
          let txt = ''
          errors.forEach(value => {
            txt += ('<p>' + value + '</p>')
          })

          window.Swal.fire({
            title: '',
            html: txt,
            icon: 'error',
            confirmButtonText: '',
            width: '40rem'
          })
            .then(result => {
              this.loaderButton = false
            })
        })
    },
    orderAfter () {
      this.loaderButtonAfter = true
      this.cashPay()
    }
  },
  computed: {
    price () {
      return this.$store.getters.priceAmount + (-this.price_with_sale + this.transfer.price) * this.$store.state.currency.ratio
    },
    priceWithoutCurrency () {
      return this.$store.getters.priceAmountWithoutCurrency + (-this.price_with_sale + this.transfer.price)
    },
    priceAmount () {
      return this.$store.getters.priceAmountWithoutCurrency
    },
    disabledButton () {
      let disabled = this.disabled

      if (this.loaderButton)
        disabled = true

      if(this.method_pay === 'cash')
        disabled = true

      return disabled
    },
    disabled () {
      let disabled = false
      for (let key in this.info) {
        console.log(!this.info[key] || this.info[key] === '')
        if (!this.info[key] || this.info[key] === '')
          disabled = true
      }

      if(!this.transfer.name || !this.method_pay || !this.info.country.id || !this.info.city.id)
        disabled = true

      return disabled
    },
    disabledButtonCode () {
      if (this.sale)
        return true

      return !this.sale && (this.code === null || this.code === '')


    },
    disabledButtonAfter () {
      let disabled = this.disabled

      if (this.loaderButtonAfter)
        disabled = true

      // if(this.method_pay === '')
      //   disabled = true

      return disabled
    },
  },
  watch: {
    'info.country': {
      handler: function (after, before) {
        this.resetTransfer()
        this.method_pay = null
      },
      deep: true
    },

    'info.city': {
      handler: function (after, before) {
        this.resetTransfer()
        this.method_pay = null
      },
      deep: true
    }
  }
}
</script>

